import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StatusReplay, CommonResponse, FileUploadResponse } from '../commmon/ServiceClass';
import { CommonVariables } from '../commmon/index';
import { map } from 'rxjs/internal/operators/map';


@Injectable({
  providedIn: 'root'
})
export class CommonService {
  constructor(private util: CommonVariables,private http:HttpClient) { }
  // check username availability
  CheckUserNameAvailability(UserName, UserID=0) {
      const url= this.util.CheckUserNameAvailability;
      const body = {'UserName': UserName, 'UserID': UserID};
      return this.http.post<StatusReplay>(url, body).pipe(map((response) => {
        return response;
      }));
  }
  // upload media into the server
  DietecMediaUpload(file: File) {
    const url = this.util.base_url + this.util.FileUpload;
    const formData = new FormData();
    formData.append('file', file);
    return this.http.post<FileUploadResponse>(url, formData)
    .pipe(map((response: FileUploadResponse) => {
      return response;
    }));
  }
  // common get
  Get(url) {
    return this.http.get<CommonResponse>(url).pipe(map((response: CommonResponse) => response));
  }
  // common post
  Post(url, body) {
    return this.http.post<CommonResponse>(url, body).pipe(map((response: CommonResponse) => response));
  }
}
